<template>
  <div class="zhuce">
    <!--  -->
    <div class="zhuce-box">
      <p></p>
      <p></p>
      <!--  -->
      <p :class="{'action':sjh,'action1':sjh1 }">
        <input @blur="shouji" v-model="sjtel" autofocus="autofocus" type="tel" placeholder="" />
        <span :class="{'span':sjh}" v-show="sjh"></span>
      </p>
      <!--  -->
      <p :class="{'action':pwd,'action1':pwd1 }">
        <input @blur="mima" v-model="pwdtel" v-show="!pwd" type="password" placeholder="" />
        <van-icon @click="pwddj" v-show="!pwd" name="closed-eye" />
        <input @blur="mima" v-model="pwdtel" v-show="pwd" type="text" placeholder="" />
        <van-icon @click="pwddj" v-show="pwd" name="eye-o" />
      </p>
      <!--  -->
      <p :class="{'action':qren2,'action1':qren1 }">
        <input @blur="qm" v-show="!qren" v-model="qrentel" type="password" placeholder="" />
        <van-icon v-show="!qren" @click="qrenmima" name="closed-eye" />
        <input @blur="qm" v-show="qren" v-model="qrentel" type="text" placeholder="" />
        <van-icon v-show="qren" @click="qrenmima" name="eye-o" />
      </p>
      <!--  -->
      <p>
        <input v-model="pictext" type="text" />
        <img :src="imgcode + key" @click="imgdj" alt />
      </p>
      <!--  -->
      <p>
        <van-field
          readonly
          clickable
          :value="value"
          placeholder=""
          @click="showPicker = true"
        />
      </p>
      <!--  -->
      <p>
        <input v-model="codhq" type="text" />
        <span v-show="!show" @click="getcodehq"></span>
        <span v-show="show">{{count}}</span>
      </p>
      <!--  -->
      <p @click="zhuce"></p>
      <!--  -->
      <p @click="login" class> </p>
      <!--  -->
      <van-popup v-model="showPicker" round position="bottom">
        <van-picker
          show-toolbar
          :columns="columns"
          @cancel="showPicker = false"
          @confirm="onConfirm"
        />
      </van-popup>
    </div>
  </div>
</template>

<script>
import { v4 as uuidv4 } from "uuid";
import qs from "qs";
import axios from "axios";
export default {
  name: "Zhuce",
  data() {
    return {
      //
      sjh: false,
      sjh1: false,
      sjtel: "",
      //
      pwd: false,
      pwd1: false,
      pwdtel: "",
      //
      qren: false,
      qren1: false,
      qren2: false,
      qrentel: "",
      //
      imgcode: "https://api.it120.cc/small4/verification/pic/get?key=",
      key: uuidv4(),
      pictext: "",
      //
      codhq: "",
      show: false,
      count: 60,
      timer: null,
      //
      value: '',
      showPicker: false,
      columns: ['', '', '', '', ''],
    };
  },
  mounted() {
    document.title = "";
  },
  methods: {
    //
    pwddj() {
      this.pwd = !this.pwd;
    },
    //   
    shouji() {
      if (this.sjtel == "") {
        this.sjh = true;
        this.$notify({ type: "warning", message: "" });
      } else if (this.sjtel != "") {
        this.sjh1 = true;
      }
    },
    // 
    mima() {
      if (this.pwdtel == "") {
        this.pwd = true;
        this.$notify({ type: "warning", message: "" });
      } else if (this.pwdtel != "") {
        this.pwd1 = true;
      }
    },
    // 
    qrenmima() {
      this.qren = !this.qren;
    },
    qm() {
      if (this.qrentel == "" || this.pwdtel === this.qrentel) {
        this.qren1 = true;
      } else {
        this.qren2 = true;
      }
    },
    //
    imgdj() {
      this.key = uuidv4();
    },
    //
    getcodehq() {
      this.show = true;
      var add = { mobile: this.sjtel, picCode: this.pictext, key: this.key };
      axios
        .post(
          "https://api.it120.cc/small4/verification/sms/get",
          qs.stringify(add)
        )
        .then(res => {
          console.log(res);
        });
      //      60
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        this.count--;
        if (this.count === 0) {
          this.show = false;
          this.count = 60;
        }
      }, 1000);
    },
    //
    zhuce() {
      var data = { mobile: this.sjtel, pwd: this.pwdtel, code: this.codhq };
      axios
        .post("https://api.it120.cc/small4/user/m/register", qs.stringify(data))
        .then(res => {
          console.log(res);
        });
    },
    //
    login() {
      this.$router.push("/login");
    },
    //
    onConfirm(value) {
      this.value = value;
      this.showPicker = false;
    },
  }
};
</script>

<style lang="scss" scoped>
.zhuce {
      padding: 0.26rem;
    _height: 2rem;
    min-height: 2rem;
  .zhuce-box {
    width: 4.8rem;
    padding: 0 0.44rem;
    // min-height: 10rem;
    border: 0.02rem solid #f5f5f5;
    margin: 0.26rem auto;
    p:nth-of-type(1) {
      font-size: 0.3rem;
      margin-top: 0.5rem;
    }
    p:nth-of-type(2) {
      font-size: 0.2rem;
      color: #909090;
      margin-top: 0.5rem;
      margin-bottom: 0.23rem;
    }
    p:nth-of-type(3) {
      width: 4.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.44rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
      padding: 0 0.2rem 0 0;
      justify-content: space-between;
      input {
        margin-left: 0.2rem;
        width: 2.6rem;
        height: 0.4rem;
        border: none;
        outline: none;
        background: #f5f5f5;
        font-size: 0.2rem;
        color: #bfbfbf;
      }
    }
    p:nth-of-type(4) {
      width: 4.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
      padding: 0 0.2rem 0 0;
      justify-content: space-between;
      input {
        margin-left: 0.2rem;
        width: 2.6rem;
        height: 0.4rem;
        border: none;
        outline: none;
        background: #f5f5f5;
        font-size: 0.2rem;
      }
    }
    .action {
      border: 0.01rem solid rgb(225, 44, 30) !important;
      // background: rgb(255, 233, 228) !important;
    }
    .action1 {
      border: 0.01rem solid rgb(56, 219, 36) !important;
      background: rgb(225, 255, 223) !important;
      input {
        background: rgb(225, 255, 223) !important;
      }
      span {
        display: none;
      }
    }
    .span {
      font-size: 0.18rem;
      color: #e12c1e;
    }
    p:nth-of-type(5) {
      width: 4.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
      padding: 0 0.2rem 0 0;
      justify-content: space-between;
      input {
        margin-left: 0.2rem;
        width: 2.6rem;
        height: 0.4rem;
        border: none;
        outline: none;
        background: #f5f5f5;
        font-size: 0.2rem;
      }
    }
    p:nth-of-type(6) {
      width: 4.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
      input {
        margin-left: 0.2rem;
        width: 1.5rem;
        height: 0.4rem;
        border: none;
        outline: none;
        background: #f5f5f5;
        font-size: 0.2rem;
      }
      img {
        width: 2rem;
        height: 0.65rem;
        position: absolute;
        right: 0;
        top: 0;
      }
    }
    p:nth-of-type(7) {
      width: 4.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
    }
    p:nth-of-type(8) {
      width: 4.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
      input {
        margin-left: 0.2rem;
        width: 2.7rem;
        height: 0.4rem;
        border: none;
        outline: none;
        background: #f5f5f5;
        font-size: 0.2rem;
      }
      span {
        display: block;
        width: 1.7rem;
        height: 0.65rem;
        border-left: 0.01rem solid #e1e1e1;
        font-size: 0.2rem;
        text-align: center;
        line-height: 0.65rem;
        cursor: pointer;
      }
    }
    p:nth-of-type(9) {
      width: 2.91rem;
      height: 0.8rem;
      border-radius: 0.06rem;
      background: -webkit-gradient(
        linear,
        left top,
        right top,
        from(#5ea6f8),
        to(#bb87f6)
      );
      background: linear-gradient(90deg, #5ea6f8, #bb87f6);
      text-align: center;
      line-height: 0.8rem;
      font-size: 0.2rem;
      color: white;
      letter-spacing: 0.06rem;
      margin: 0 auto;
      margin-top: 0.24rem;
    }
    p:nth-of-type(10) {
      font-size: 0.2rem;
      color: #549ff9;
      margin-top: 0.27rem;
      text-align: center;
    }
  }
}
</style>


